ht: 50%;\n  margin: 2em -50vw;\n  background-size: cover;\n  background-position: bottom;\n  display: flex;\n  justify-content: center;\n  align-items: center; }\n\n.full-width-image {\n    width: 100vw;\n    height: 400px;\n    background-size: cover;\n    background-position: bottom;\n    display: flex;\n    justify-content: center;\n    align-items: center; }\n\n.btn {\n    display: inline-block;\n    padding: 12px 16px 10px;\n    font-size: 18px;\n    font-size: 1rem;\n    line-height: 1.25;\n    background-color: #fff;\n    border-radius: .25rem;\n    text-decoration: none;\n    font-weight: 700;\n    color: #CC3700;\n    text-align: center;\n    -webkit-box-shadow: inset 0 0 0 2px #CC3700;\n    box-shadow: inset 0 0 0 2px #f40;\n    -webkit-transition: all .15s ease;\n    transition: all .15s ease; }\n\n.margin-top-0 {\n  margin-top: 0 !important; }\n\n.navbar-item .icon {\n  color: $primary; }\n// Override for use of svg's from https://simpleicons.org/\n.icon svg {\n  width: 1.5rem;\n  height: 1.5rem;\n  fill: currentColor; }\n.navbar-brand .navbar-item.logo {\n  padding: 0 1rem; }\nfooter.footer {\n  padding: 3rem 0rem 0rem;\n  background-color: transparent; }\n\n//Menu overrides\n$menu-item-color: $white-ter;\n$menu-item-hover-color: $black !important;\n$menu-item-hover-background-color: $black;\n$menu-item-active-color: $kaldi-red-invert;\n$menu-item-active-background-color:\t$kaldi-red-invert;\n$menu-list-border-left: 1px solid $kaldi-red-invert;\n$menu-label-color: $white-ter;\n\n\n.menu-label {\n  font-size: 1em !important;\n  text-align: left; }\n.menu-list {\n  list-style: none !important;\n  text-align: left; }\n.social {\n  padding: 2em; }\n.social a {\n    padding: .5em .5em .3em .5em;\n    border-radius: 1em;\n    background-color: $white-ter;\n    margin: .5em;\n    width: 1em;\n    height: 1em;\n    vertical-align: middle;\n    display: inline; }\n\n// blog roll\n.blog-list-item.is-featured {\n  background-color: #d6400033; }\n.blog-list-item header {\n  display: flex;\n  margin-bottom: 1em; }\n.blog-list-item .featured-thumbnail {\n  flex-basis: 35%;\n  margin: 0 1.5em 0 0; }\n\n\n@import \"~bulma\";\n\n\n// responsiveness\n@include tablet-only {\n  .blog-list-item .featured-thumbnail {\n    flex-basis: 50%; } }\n\n@include mobile {\n  .blog-list-item header {\n    display: block; }\n  .blog-list-item .featured-thumbnail {\n    text-align: center;\n    max-width: 70%;\n    margin: 0 0 1em; } }\n","// Colors\n\n$black:        hsl(0, 0%, 4%) !default;\n$black-bis:    hsl(0, 0%, 7%) !default;\n$black-ter:    hsl(0, 0%, 14%) !default;\n\n$grey-darker:  hsl(0, 0%, 21%) !default;\n$grey-dark:    hsl(0, 0%, 29%) !default;\n$grey:         hsl(0, 0%, 48%) !default;\n$grey-light:   hsl(0, 0%, 71%) !default;\n$grey-lighter: hsl(0, 0%, 86%) !default;\n$grey-lightest: hsl(0, 0%, 93%) !default;\n\n$white-ter:    hsl(0, 0%, 96%) !default;\n$white-bis:    hsl(0, 0%, 98%) !default;\n$white:        hsl(0, 0%, 100%) !default;\n\n$orange:       hsl(14,  100%, 53%) !default;\n$yellow:       hsl(48,  100%, 67%) !default;\n$green:        hsl(141, 53%,  53%) !default;\n$turquoise:    hsl(171, 100%, 41%) !default;\n$cyan:         hsl(204, 71%,  53%) !default;\n$blue:         hsl(217, 71%,  53%) !default;\n$purple:       hsl(271, 100%, 71%) !default;\n$red:          hsl(348, 86%, 61%) !default;\n\n// Typography\n\n$family-sans-serif: BlinkMacSystemFont, -apple-system, \"Segoe UI\", \"Roboto\", \"Oxygen\", \"Ubuntu\", \"Cantarell\", \"Fira Sans\", \"Droid Sans\", \"Helvetica Neue\", \"Helvetica\", \"Arial\", sans-serif !default;\n$family-monospace: monospace !default;\n$render-mode: optimizeLegibility !default;\n\n$size-1: 3rem !default;\n$size-2: 2.5rem !default;\n$size-3: 2rem !default;\n$size-4: 1.5rem !default;\n$size-5: 1.25rem !default;\n$size-6: 1rem !default;\n$size-7: 0.75rem !default;\n\n$weight-light: 300 !default;\n$weight-normal: 400 !default;\n$weight-medium: 500 !default;\n$weight-semibold: 600 !default;\n$weight-bol